import React, { Component } from 'react'
import { Offline, Online } from 'react-detect-offline'
import { Breath } from './Breath'
import { ErrorModal } from './ErrorModal'
import { Modal } from './Modal'
import { AnimatedEarth } from './AnimatedEarth'

import ThumbPrint from './thumbprint--gray.svg'

// import X from './x.svg'

import './App.css'

class App extends Component {
  constructor(props) {
    super(props)

    this.state = { isInstructions: false }
  }

  handleClick(event) {
    event.preventDefault()

    let isInstructions = this.state.isInstructions

    this.setState({ isInstructions: !isInstructions })

    if (!isInstructions) {
      event.currentTarget.style.opacity = 1
    } else {
      event.currentTarget.style.opacity = 0
    }
  }

  render() {
    let __DEV__ = process.env.NODE_ENV === 'development' ? true : false
    return (
      <div>
        {__DEV__ ? (
          <div>
            <Online>
              <AnimatedEarth />
            </Online>
            <Offline>
              <ErrorModal />
            </Offline>
          </div>
        ) : (
          <div>
            <Online>
              <ErrorModal />
            </Online>
            <Offline>
              <AnimatedEarth />
            </Offline>
          </div>
        )}
      </div>
    )
  }
}

export default App



