import React, { Component } from 'react'

import { WifiOff } from 'react-feather'

import './Modal.css'

export class ErrorModal extends Component {
  render() {
    return (
      <div className="Modal">
            <h1>The World Offline</h1>
        <WifiOff color="#000000" size={55} />
        <div class="textbox">
          <p>Please disable your internet connection to continue</p>
        </div>
      </div>
    )
  }
}



